.not-found-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #f4f7fb;
  text-align: center;
  padding: 20px;
}

.not-found-content {
  max-width: 500px;
}

.error-code {
  font-size: 8rem;
  font-weight: 800;
  color: #492fb2; /* theme color */
  margin-bottom: 10px;
}

.error-title {
  font-size: 2rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 15px;
}

.error-message {
  font-size: 1.1rem;
  color: #666;
  margin-bottom: 30px;
}
